.mangaArea {
  display: grid;
  flex-wrap: wrap;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: 350px;
  grid-gap: 2vmin;
  margin: 0 100px;
  width: 1280px;
}

.MainPage {
  width: max-content;
  margin: 30px auto 0 auto;
  display: flex;
  align-items: center;
}
.MainPage .paginationButton {
  height: 200px;
  width: 100px;
}

.pageNumber {
  width: max-content;
  margin: 10px auto 0 auto;
}

